<template>
  <div id="app">
    <nav class="navbar navbar-expand-lg navbar-light bg-light fixed-top">
      <div class="container-fluid">
        <a class="navbar-brand" href="#">Navbar</a>
      </div>
    </nav>
    <br><br><br>
    <div class="container">
      <Clickpage @updated_draw="self_update_draw" :draw_count="sellected_times" :draw_type="sellected_type"/>
      <br>
      <Show :pass_in_array="draw_array" :pass_in_count="draw_array.length" @back_to_clickpage="self_update_status"/>
      <br>
    </div>
    
  </div>
</template>

<script>
import Clickpage from './components/Clickpage'
import Show from './components/Show'

export default {
  name: 'App',
  components: {
    Clickpage,
    Show
  },
  data(){
    return{
      draw_array: [],
      status:0,
      sellected_type:"0",
      sellected_times:"1"
    }
  },
  methods:{
    self_update_draw({array, count, type}){
      this.draw_array=array;
      this.sellected_type = type;
      this.sellected_times = count;
      this.status = 1;
    },
    self_update_status(val){
      this.status = val;
    }
  }
}
</script>

