<template>
    <div>
        <img :src="src" style="margin:0 auto;" :width="current_width">
    </div>
</template>

<script>
export default {
    name:'Displaycard',
    props:{
        name: String,
        src: String,
        parent_width: Number
    },
    computed:{
        current_width(){
            return this.parent_width / 5.5;
        }
    }
}
</script>