<template>
    <div>
        <div class="card">
            <div class="card-body">
                <form class="d-grid gap-2 col-6 mx-auto">
                    <div class="mb-3">
                        <label class="form-label">模式</label>
                        <select class="form-select" aria-label="card_count_select" v-model="this_draw_type">
                            <option value="0">一般</option>
                            <option value="1">加倍</option>
                        </select>
                    </div>
                    <div class="mb-3">
                        <label class="form-label">抽卡張數</label>
                        <select class="form-select" aria-label="card_count_select" v-model="this_draw_count">
                            <option value="1">單抽</option>
                            <option value="10">10抽</option>
                            <option value="35">35抽</option>
                        </select>
                    </div>
                </form>
                <br>
                <div class="d-grid gap-2 col-6 mx-auto">
                    <button class="btn btn-primary" type="button" @click="draw">Button</button>
                </div>
            </div>
        </div>
       
        
    </div>
</template>

<script>
export default {
    name: 'Clickpage',
    props:{
        draw_type: String,
        draw_count: String
    },
    data(){
        return{
            this_draw_count: this.draw_count,
            this_draw_type: this.draw_type,
            draw_array:[],
            card_detail:
            {
                "0": 
                {
                    "name":"夢詠守望 ‧ 英格麗",
                    "c":"5",
                    "src":"https://i.imgur.com/Qex60jq.jpg"
                },
                "1":
                {
                    "name":" 緋曦赤霞 ‧ 紅璦",
                    "c":"2",
                    "src":"https://i.imgur.com/YNkZD8A.jpg"
                },
                "2":
                {
                    "name":"比擬萬象 ‧ 達格",
                    "c":"3",
                    "src":"https://i.imgur.com/NrMUjwO.jpg"
                },
                "3":
                {
                    "name":"水幻冰藍 ‧ 米菲波",
                    "c":"1",
                    "src":"https://i.imgur.com/ssgcz6l.jpg"
                },
                "4":
                {
                    "name":"黛蔻藥師 ‧ 茜蘿珊",
                    "c":"3",
                    "src":"https://i.imgur.com/PhmqsYz.jpg"
                },
                "5":{
                    "name":"天才使魔 ‧ 豹豹",
                    "c":"4",
                    "src":"https://i.imgur.com/laMBtZh.jpg"
                },
                "6":{
                    "name":"破幻銀鈴 ‧ 傾霞",
                    "c":"4",
                    "src":"https://i.imgur.com/B6Gjqvq.jpg"
                },
                "7":{
                    "name":"無微珍護 ‧ 曼特裘",
                    "c":"5",
                    "src":"https://i.imgur.com/LgzuGQl.jpg"
                }
            }
        }
    },
    methods:{
        draw(){
            var waiting_array = new Array();
            var new_draw_array = new Array();
            this.draw_array = new_draw_array;
            var c1, c2, c3;
            if(this.this_draw_type == 0){
                c1 = 10; c2 = 25; c3 = 188;
            }else{
                c1 = 25; c2 = 100; c3 = 155;
            }
            for(var i1 = 0; i1 < c1; i1++){
                waiting_array.push(0);
            }
            for(var j2 = 1; j2 <= 2; j2++){
                for(var i2 = 0; i2 < c2; i2++){
                    waiting_array.push(j2);
                }
            }
            for(var j3 = 3; j3 <= 7; j3++){
                for(var i3 = 0; i3 < c3; i3++){
                    waiting_array.push(j3);
                }
            }

            for(var draw_count = 0; draw_count < this.this_draw_count; draw_count++){
                var random_number=Math.floor(Math.random()*(waiting_array.length));
                var picked_item = waiting_array[random_number];
                new_draw_array.push({'id':draw_count, 'name':this.card_detail[picked_item].name, 'src':this.card_detail[picked_item].src});
            }
            this.draw_array = new_draw_array;
            this.update_draw_array();
        },
        update_draw_array(){
            this.$emit("updated_draw", {'array':this.draw_array, 'count': this.this_draw_count, 'type': this.this_draw_type})
        }
    }
}
</script>

<style></style>